'use strict';

/**
* Geometric distributed pseudorandom numbers.
*
* @module @stdlib/random-base-geometric
*
* @example
* import geometric from '@stdlib/random-base-geometric';
*
* var v = geometric( 0.5 );
* // returns <number>
*
* @example
* import { factory as factory } from '@stdlib/random-base-geometric';
*
* var geometric = factory( 0.3, {
*     'seed': 297
* });
*
* var v = geometric();
* // returns <number>
*
* @example
* import { factory as factory } from '@stdlib/random-base-geometric';
*
* var geometric = factory({
*     'seed': 297
* });
*
* var v = geometric( 0.5 );
* // returns <number>
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import factory from './factory.js';


// MAIN //

setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;
